import React, { useState, useEffect } from 'react'
import CitizenApplyApiList from '../../Components/CitizenApplyApiList'
import PropertySidebar from '../Component/ModuleWiseSidebar/PropertySidebar'
import brief from '../../../src/Components/Media/brief.png'
import { ColorRing } from "react-loader-spinner";


import axios from 'axios'
import AxiosInterceptors from '../../Components/GlobalData/AxiosInterceptors';

function PropertyTranscation() {

  const [loaderStatus, setloaderStatus] = useState(false)

    const { api_getTranscationHistory } = CitizenApplyApiList()

    const [transcationList, settranscationList] = useState()

    let token = window.localStorage.getItem('token')
    // let token = "1753|dYxpBvHkpGZTmSC659vRSh6eBfscWDQEwjSDQGU3"
    const header = {
        headers:
        {
            Authorization: `Bearer ${token}`,
            Accept: 'application/json',
        }
    }

    //{////********ApplicationList*******//////}
    const fetchTranscationList = () => {
        setloaderStatus(true)
        AxiosInterceptors.get(api_getTranscationHistory,
            // {
            //     module: module.module
            // },
            {
                headers:
                {
                    Authorization: `Bearer ${token}`,
                    Accept: 'application/json',
                }
            }
        )
            .then(function (response) {
                console.log("all transcation list----- ", response.data.data);
                settranscationList(response.data.data)
                setloaderStatus(false)

            })
            .catch(function (error) {
                console.log(error);
                setloaderStatus(false)
            })

    }

    useEffect(() => {
        fetchTranscationList();
    }, [])

    return (
        <>
          {loaderStatus && (
        <div className="w-full z-10 absolute mx-auto text-center flex justify-center items-center top-1/2">
          <span className="inline">
            <ColorRing
              visible={true}
              height="120"
              width="120"
              ariaLabel="blocks-loading"
              wrapperStyle={{}}
              wrapperClass="blocks-wrapper"
              colors={["#e15b64", "#f47e60", "#f8b26a", "#abbd81", "#849b87"]}
            />
          </span>
        </div>
      )}
            <div className='grid grid-cols-1 md:grid-cols-12 lg:grid-cols-12 gap-2 mx-auto  w-full'>
                {/* <div className='w-full col-span-2'>
                    <PropertySidebar />
                </div> */}
                <div className='col-span-10 bg-gray-50 p-4'>
                    <div className='flex flex-row'>
                        <h1 className='px-1 font-semibold font-serif text-2xl mt-4'><img src={brief} alt="pin" className='w-8 inline' />All Transcation Related To Property</h1>
                    </div>

                    <table className='min-w-full leading-normal mt-10'>
                        <thead className='font-bold text-left text-sm bg-green-50 text-gray-600'>
                            <tr>
                                <th className="px-2 py-3 border-b border-gray-200  text-left text-xs uppercase text-left">#</th>
                               
                                <th className="px-2 py-3 border-b border-gray-200  text-left text-xs uppercase text-left">Saf No.</th>
                               
                                <th className="px-2 py-3 border-b border-gray-200  text-left text-xs uppercase text-left">Transcation No.</th>
                                <th className="px-2 py-3 border-b border-gray-200  text-left text-xs uppercase text-left">Transcation Mode</th>
                                <th className="px-2 py-3 border-b border-gray-200  text-left text-xs uppercase text-left">Transcation Date</th>
                                <th className="px-2 py-3 border-b border-gray-200  text-left text-xs uppercase text-left">Amount</th>
                               

                            </tr>
                        </thead>
                        <tbody className="text-sm">

                            <>
                                {transcationList?.map((items,index) => (
                                    <tr className="bg-white shadow-lg border-b border-gray-200">
                                        <td className="px-2 py-2 text-sm text-left">{index+1}</td>
                                        
                                        <td className="px-2 py-2 text-sm text-left">{items.saf_no}</td>
                                        <td className="px-2 py-2 text-sm text-left">{items.tran_no}</td>
                                        <td className="px-2 py-2 text-sm text-left">{items.payment_mode}</td>
                                        <td className="px-2 py-2 text-sm text-left">{items.tran_date}</td>
                                        <td className="px-2 py-2 text-sm text-left">{items.amount}</td>
                                     
                                    </tr>
                                ))}

                            </>

                        </tbody>
                    </table>

                </div>
            </div>
        </>
    )
}

export default PropertyTranscation